import { Course, Section } from './types'

export interface SelectedCourse {
    course: Course;
    selectedSection: Section | null;  // Store the full Section object for direct access
    selectedSectionNumber: string | null;  // Keep for backward compatibility and serialization
    isRequired: boolean;
}

export interface Schedule {
    id: string;
    name: string;
    selectedCourses: SelectedCourse[];
    generatedSchedules: ScheduleCombination[];
}

export interface ScheduleCombination {
    id: string;
    sections: Section[];
    conflicts: TimeConflict[];
    isValid: boolean;
}

export interface TimeConflict {
    section1: Section;
    section2: Section;
    conflictType: ConflictType;
    description: string;
}

export interface SchedulePreferences {
    preferredTimeRange: {
        startTime: { hours: number; minutes: number };
        endTime: { hours: number; minutes: number };
    };
    preferredDays: Set<string>;
    avoidBackToBackClasses: boolean;
    theme?: string;
}

export enum ConflictType {
    TIME_OVERLAP = 'time_overlap'
}

export interface UserScheduleState {
    activeSchedule: Schedule | null;
    savedSchedules: Schedule[];
    preferences: SchedulePreferences;
}